<template>
  <section id="device_div">
    <el-row>
      <el-col :span="24">
        <!---->
        <el-form :inline="true" :model="params" class="demo-form-inline" style="height: 90px;background: #ffffff;margin-bottom: 15px;box-shadow:0px 0px 18px #d6d6d6;">
          <div style="padding: 25px 0 0 0px">
          <el-form-item label="">
            <el-input v-model="params.instrumentName" placeholder=""></el-input>
          </el-form-item>
          
          <el-form-item label="">  
            <el-select v-model="params.deviceTypeCode" placeholder="">
              <el-option
                v-for="item in sblxs"
                :key="item.dicValueCode"
                :label="item.dicValueName"
                :value="item.dicValueCode">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="">
            <el-input v-model="params.orgnizationName" placeholder=""></el-input>
          </el-form-item>

          <!--<el-form-item label="" >  
            <el-select v-model="params.orgnizationName" placeholder="" width=150>
              <el-option
                v-for="item in options"
                :key="item.orgnizationCode"
                :label="item.orgnizationName"
                :value="item.orgnizationName">
              </el-option>
            </el-select>
          </el-form-item>-->
          <el-button type="primary" icon="search" @click="searchDevice"></el-button>
          <el-button type="primary" @click="reset"></el-button>
          <el-button type="primary" icon="plus" @click="addDevice"></el-button>
        </div>
        </el-form>
        
        <!---->
        <div style="background: #ffffff;box-shadow:0px 0px 18px #d8d8d8;padding: 30px 0px">
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          </el-table-column>

         <!--<el-table-column type="selection"/>-->


          <el-table-column
            prop="instrumentName"
            label=""
            sortable
            width="150">
          </el-table-column>
          <el-table-column
            prop="deviceTypeCode"
            label=""
            sortable
            :formatter="yblxFormat"
            width="150">
          </el-table-column>
          <el-table-column
            prop="instrumentModel"
            label=""
            sortable
            width="150">
          </el-table-column>
          <el-table-column
            prop="instrumentCode"
            label=""
            sortable
            width="150">
          </el-table-column>
          <el-table-column
            prop="organizationCode"
            label=""
            sortable
            :formatter="dwFormat"
            width="150">
          </el-table-column>
          <el-table-column
            prop="deviceStatusCode"
            label="" :formatter="sbztFormat"
            sortable
            width="150">
          </el-table-column>

          <el-table-column label="" width="150">
            <template scope="scope">
              <el-button type="primary" size="small" @click="editDevice(scope.$index, scope.row)"></el-button>
              <el-button type="danger" size="small" @click="delDevice(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="sizeChange"
            @current-change="currentChange"
            :current-page="params.pageNum"
            :page-sizes="[10, 30, 50, 100]"
            :page-size="params.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
      </el-col>
    </el-row>

    <el-dialog :title="dialogTitle" v-model="dialogFormVisible" size="tiny">
      <device-form 
        :dialog-type="dialogType"

        ref="deviceForm" 
        v-on:close="cancelDevice">  
      </device-form>

      <el-form ref="deviceForm" :model="deviceForm" label-width="80px">

        <el-form-item label="">
          <el-input v-model="deviceForm.instrumentName"></el-input>
        </el-form-item>
        <el-form-item label="">
        <el-select style="width: 100%" v-model="deviceForm.deviceTypeCode" placeholder="">
              <el-option
                v-for="item in sblxs_mod"
                :key="item.dicValueCode"
                :label="item.dicValueName"
                :value="item.dicValueCode">
              </el-option>
        </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="deviceForm.instrumentModel"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="deviceForm.instrumentCode" @change="check" ></el-input>
        </el-form-item>
        
        <el-form-item label="">
          <el-select style="width: 100%" v-model="deviceForm.organizationCode" placeholder="">
            <el-option
                v-for="item in options"
                :key="item.orgnizationCode"
                :label="item.orgnizationName"
                :value="item.orgnizationCode">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-select style="width: 100%" v-model="deviceForm.deviceStatusCode" placeholder="">
            <el-option
                v-for="item in sbzts"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>


        <el-form-item>
            <el-button @click="saveDevice"> </el-button>
            <el-button @click="cancelDevice"> </el-button>
        </el-form-item>       
      </el-form>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data () {
    return {
      params: {
        instrumentName: null,
        deviceTypeCode: null,
        organizationCode: null,
        pageNum: 1,
        pageSize: 10
      },
      params1: {
        orgnizationName: null,
        orgnizationDegree: null,
        pageNum: 1,
        pageSize: 10
      },
      params2: {
        pageNum: 1,
        pageSize: 10
      },
      tableData: [],
      
      dialogFormVisible: false,
      editLoading: false,
      deviceForm: {
        instrumentName: '',
        deviceTypeCode: '',
        instrumentModel: '',
        instrumentCode: '',
        organizationCode: '',
        deviceStatusCode: '',
      },
      total: 0,
      table_index: 999,
      dialogType:'',
      dialogTitle:'',
      show:false,
      device_loading:"",
      options: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        },
      ],
      sblxs:[],
      sblxs_mod:[],

      sbzts: [{
          value: 'DS-IN-01',
          label: ''
        }, {
          value: 'DS-REPAIRE-02',
          label: ''
        }, {
          value: 'DS-OUT-01',
          label: ''
        }, {
          value: 'DS-OFF-01',
          label: ''
        },
      ],

    };
  },
  created () {
    
    this.device_loading = this.$loading({
      lock:true,
      text:'loading',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)',
      target: document.querySelector('#device_div')
    });

    this.getAllOrgnization();
    this.getAllDeviceType();
    this.searchDevice ();
  },
  methods: {
    yblxFormat(row, column) {
      var deviceTypeCode = row['deviceTypeCode'];

      for(var x in this.sblxs){
       if(this.sblxs[x].dicValueCode==deviceTypeCode)
       {
       return this.sblxs[x].dicValueName;
       }
     }
    },
    dwFormat(row, column) {
      var organizationCode = row['organizationCode'];

      for(var x in this.options){
       if(this.options[x].orgnizationCode==organizationCode)
       {
       return this.options[x].orgnizationName;
       }
     }
    },
    sbztFormat(row, column) {
      var deviceStatusCode = row['deviceStatusCode'];
      if (deviceStatusCode === undefined) {
        return "";
      }

      if(deviceStatusCode=='DS-IN-01')
      {
          return ""  ;
      }
      else if(deviceStatusCode=='DS-REPAIRE-02')
      {
          return ""  ;
      }
      else if(deviceStatusCode=='DS-OUT-01')
      {
          return ""  ;
      }
      else if(deviceStatusCode=='DS-OFF-01')
      {
          return ""  ;
      }
    },
    
    getAllOrgnization(){
      this.params1.orgnizationDegree=null;
      this.params1.orgnizationName=null;
      var p = JSON.parse(JSON.stringify(this.params1));
      var url = '/orgnization/pages?pageNum=1&pageSize=1000';
      
      this.$http.post(url, p).then((response) => {
        var pageUser = response.data;
        this.options = pageUser.list;

      });
    },
    getAllDeviceType(){
      var p = JSON.parse(JSON.stringify(this.params2));
      var url ='/baseData/dic/deviceType/pages?pageNum=1&pageSize=1000';
      
      this.$http.post(url, p).then((response) => {
        var pageUser = response.data;
        this.sblxs = pageUser.list;
        this.sblxs_mod.splice(0,this.sblxs_mod.length);
        //DT-SHIELD-01
        for(var i in pageUser.list){
          var x = pageUser.list[i];
          if(x.dicValueCode!='DT-SHIELD-01'){
          //console.log(x);
          this.sblxs_mod.push(x);
          }
        }
      });
    },
    //
    // 
    // 
    searchDevice () {
      if(this.params.instrumentName==''){
        this.params.instrumentName=null;
      }
      if(this.params.deviceTypeCode==''){
        this.params.deviceTypeCode=null;
      }
      if(this.params.organizationCode==''){
        this.params.organizationCode=null;
      }
      if(this.params.orgnizationName==''){
        this.params.organizationCode=null;
      }
      console.log(this.params);
      var url = '/instrument/pages?pageNum='+this.params.pageNum+'&pageSize='+this.params.pageSize;
      var p = JSON.parse(JSON.stringify(this.params));
      //this.$http.post('/driver/getDrivers.do', p).then((response) => {
        this.$http.post(url, p).then((response) => {
        var pageUser = response.data;
        this.total = pageUser.total;
        this.params.pageNum = pageUser.pageNum;

        this.tableData = pageUser.list;

        this.device_loading.close();
      });
    },
    reset () {
      this.params.instrumentName = '';
      this.params.deviceTypeCode = '';
      this.params.organizationCode = '';
      this.params.orgnizationName = '';
      this.searchDevice();
    },
    // deleteAll () {
    //   this.$message.error('!');
    // },

    // 
    delDevice (index, row) {
      if(row.deviceStatusCode=='DS-OUT-01'){
        alert('');
        return;
      }
      
          var url = '/instrument/dsdevice/?instrumentCode='+row['instrumentCode'];
          this.$http.delete(url).then((response) => {
          //this.$http.post('/driver/removeDriver.do', row['gh']).then((response) => {
          // 
          var isRemovedOk = response.data;
          if(isRemovedOk){
                this.$message({
                 message: "",
                 type: 'success'
                 });
                this.tableData.splice(index, 1);
                }else{
                  this.$message.error('!'); 
                }
           });
    },

    editDevice (index, row) {
      this.dialogType = 'edit'; 
      this.dialogTitle = '';

      if(row.deviceStatusCode=='DS-OUT-01'){
        alert('');
        return;
      }
      var rowTmp =  [].concat(JSON.parse(JSON.stringify(row)));

      this.deviceForm = Object.assign({}, rowTmp[0]);
      this.dialogFormVisible = true;
      this.dialogFormTitle="123";
      this.table_index = index;
    },

    // 
    addDevice () {
      this.dialogType = 'add';   
      this.dialogTitle = '';   
      this.deviceForm = Object.assign({}, );
      this.dialogFormVisible = true;
      // this.form = Object.assign({}, row);
      // this.table_index = index;
    },
    // 
    cancelDevice(){
      this.dialogFormVisible = false;    //el-dialog
    },

    saveDevice(){
      var _this= this
          switch(this.dialogType){
             // 
            case 'add':
                {
                  if(!this.deviceForm.instrumentName||this.deviceForm.instrumentName==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.deviceTypeCode||this.deviceForm.deviceTypeCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.instrumentCode||this.deviceForm.instrumentCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.organizationCode||this.deviceForm.organizationCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.deviceStatusCode||this.deviceForm.deviceStatusCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  var rowTmp =  [].concat(JSON.parse(JSON.stringify(this.deviceForm)));
                  var p = JSON.parse(JSON.stringify(rowTmp[0]));

                  this.$http.post('/instrument/', p).then((response) => {
                    // 
                      var isAddedOk = response.data;
                      if(isAddedOk){
                          this.$message({
                          message: "",
                          type: 'success'
                          });
                          
                          this.dialogFormVisible = false;
                          _this.searchDevice();
                      }else{
                        this.$message.error('!'); 
                      }
                  });
                }
             break;
            // 
            case 'edit':
                {
                  if(!this.deviceForm.instrumentName||this.deviceForm.instrumentName==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.deviceTypeCode||this.deviceForm.deviceTypeCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.instrumentCode||this.deviceForm.instrumentCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.organizationCode||this.deviceForm.organizationCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  if(!this.deviceForm.deviceStatusCode||this.deviceForm.deviceStatusCode==''){
                    this.$message.error('!'); 
                    return;
                  }
                  
                  var rowTmp =  [].concat(JSON.parse(JSON.stringify(this.deviceForm)));

                  var p = JSON.parse(JSON.stringify(rowTmp[0]));

                  this.$http.put('/instrument/dsdevice', p).then((response) => {
                    // 
                      var isUpdatedOk = response.data;
                      if(isUpdatedOk){
                          this.$message({
                          message: "",
                          type: 'success'
                          });
                          this.dialogFormVisible = false;
                          _this.searchDevice();
                      }else{
                        this.$message.error('!'); 
                      }
                  });
                } 
              break;
          }
    },

    
    sizeChange(val) {
      this.params.pageSize = val;
      this.searchDevice();
    },
    currentChange(val) {
      this.params.pageNum = val;
      this.searchDevice();
    },
    check(e) {
      this.$nextTick(() => {
        this.deviceForm.instrumentCode = e.replace(/[^a-zA-Z0-9_-]/g,'')
      })
      
    }, 
  },
  
}

</script>
<style>
  .el-pagination {
    text-align: center;
    margin-top: 30px;
  }
  .el-message-box__btns .cancel {
    float: right;
    margin-left: 10px;
  }
</style>



