<template>
  <section>
    <el-row>
      <el-col :span="24">
        <!---->
        <el-form :inline="true" :model="params" class="demo-form-inline" style="height: 90px;background: #ffffff;margin-bottom: 15px;box-shadow:0px 0px 18px #d6d6d6;">
          <div style="padding: 25px 0 0 50px">
          <el-form-item label="">
            <el-input v-model="params.dsdeviceName" placeholder=""></el-input>
          </el-form-item> 
          <el-form-item label="">
            <el-input v-model="params.orgnizationName" placeholder=""></el-input>
          </el-form-item>
          <el-button type="primary" icon="search" @click="searchDevice"></el-button>
          <el-button type="primary" @click="reset"></el-button>
          <el-button type="primary" icon="plus" @click="addDevice"></el-button>
        </div>
        </el-form>
        
        <!---->
        <div id="shieldmgr_div" style="background: #ffffff;box-shadow:0px 0px 18px #d8d8d8;padding: 30px 0px">
        <el-table
          :data="tableData"
          border
          style="width: 100%">

          <el-table-column label="" width="100">
            <template scope="scope">
              <el-button :disabled="scope.row.dsdeviceSerialNo != 'N/A'" type="primary" size="small" @click="devicecomm(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>

          <el-table-column
            prop="deviceTypeCode"
            label=""
            :formatter="devtypeFormat"
            width="150">
          </el-table-column>
          <el-table-column
            prop="dsdeviceName"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="dsdeviceModel"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="dsdeviceSerialNo"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="dsdeviceCode"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="orgnizationName"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="deviceStatusCode"
            :formatter="devstatusFormat"
            label=""
            width="130">
          </el-table-column>
          
          <el-table-column label="" width="150">
            <template scope="scope">
              <el-button type="primary" size="small" @click="editShield(scope.$index, scope.row)"></el-button>
              <el-button type="danger" size="small" @click="delShield(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>

        </el-table>
        <div class="block">
          <el-pagination
            @size-change="sizeChange"
            @current-change="currentChange"
            :current-page="params.pageNum"
            :page-sizes="[10, 30, 50, 100]"
            :page-size="params.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
      </el-col>
    </el-row>

    <el-dialog @close="cancelDevice" :title="dialogType =='edit'?'':''" v-model="dialogFormVisible" size="tiny">
      <user-form 
        :dialog-type="dialogType"
        ref="deviceForm">  
      </user-form>

      <el-form ref="deviceForm" :model="deviceForm" label-width="80px">

        <el-form-item label="">
          <!-- <el-input v-model="deviceForm.deviceTypeCode"></el-input> -->
          <el-select disabled="true" v-model="deviceForm.deviceTypeCode" placeholder="" style="width:100%">
              <el-option
                v-for="(val,key) in dics['3*']"
                :key="key"
                :label="val"
                :value="key">
              </el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="deviceForm.dsdeviceName"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="deviceForm.dsdeviceModel"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input disabled="true" v-model="deviceForm.dsdeviceSerialNo" placeholder="N/A"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input clearable="true" @change="dsdeviceCodeyz" v-model="deviceForm.dsdeviceCode"></el-input>
        </el-form-item> 
        <el-form-item label="">
          <!-- <el-input v-model="deviceForm.organizationCode"></el-input> -->
            <el-select v-model="deviceForm.organizationCode" placeholder="" style="width:100%">
              <el-option
                v-for="(object,i) in orgn"
                :key= "i"
                :label="object.orgnizationName"
                :value="object.orgnizationCode">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="deviceForm.deviceStatusCode" placeholder="" style="width:100%">
              <el-option
                v-for="(val,key) in dics['4*']"
                :key="key"
                :label="val"
                :value="key">
              </el-option>
            </el-select>
        </el-form-item> 

        <el-form-item>
            <el-button @click="saveDevice"> </el-button>
            <el-button @click="cancelDevice"> </el-button>
        </el-form-item>       
      </el-form>
    </el-dialog>

    <el-dialog title="" v-model="dialogFormVisiblex"  style="height:700px; width:1500px">
      <!-- <user-form 
        :dialog-type="dialogType"
        ref="WorkOrderForm" 
        v-on:close="cancelWorkOrder">  
      </user-form> -->
      <div>
        <h2></h2>
        <ul>
          <li>1</li>
          <li>2(2)</li>
          <li>3</li>
          <li>3.15</li>
          <li>3.2</li>
        </ul>
        <!-- <div>
          <h3></h3>
          <el-button type="primary"></el-button>
          <el-button type="danger"></el-button>
        </div> -->
        <el-card class="box-card" style="margin-top: 30px">
        <div slot="header" class="clearfix">
          <span></span>
          <el-button style="float: right; padding: 5px 5px;margin: 0 20px" @click="devicecommex()" type="primary"></el-button>
           <el-button  style="float: right; padding: 5px 5px"  type="danger"></el-button>
        </div>
        <div  ref="o" style="width: 100%;cursor:pointer" @click="radioss(o,index)" v-for="(o,index) in newArr" :key="o" >
           {{o}}
        </div>
      </el-card>

      </div>
      
    </el-dialog>
  </section>
</template>

<script>
export default {
  data () {
    return {
      params: {
        deviceTypeCode: null,
        dsdeviceName: null,
        orgnizationName: null,
        pageNum:1,
        pageSize:10,
      },
      tableData: [],
      dialogFormVisiblex:false,    
      dialogFormVisible: false,
      editLoading: false,
      deviceForm: {
        deviceStatusCode: '',
        deviceTypeCode: '',
        dsdeviceCode: '',
        dsdeviceModel: '',
        dsdeviceName: '',
        dsdeviceSerialNo: 'N/A',        
        organizationCode: '', 
        orgnizationName : '',      
      },
      total: 0,
      table_index: 999,
      dialogType:'',
      newArr:[],
      radios:"",
      radio_index:0,
      deviceID:"",
      shieldmgr_loading:""
    };
    
  },

computed: {
      dsdeviceCode() {
          return this.$store.state.indexstore.dsdeviceCode
      },
      arr(){
         return this.$store.state.indexstore.arr
      },
      dics(){
        return this.$store.state.indexstore.dics
      },
      orgn(){
          return this.$store.state.indexstore.orgn
      },
  },
  beforeMount(){

    console.log(this.dsdeviceCode,this.arr,this.dics['3*'])
    // if(!this.$store.state.indexstore.user){
    //     this.$router.push("/login")
    // }
    // this.$store.state.indexstore.dsdeviceCode = 5
  },

  created () {
    this.shieldmgr_loading = this.$loading({
      lock: true,
      text: 'Loading',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)',
      target: document.querySelector('#shieldmgr_div')
    });
    /*setTimeout(() => {
      this.shieldmgr_loading.close();
    }, 2000);*/
    this.searchDevice ();
  },
  methods: {

     devtypeFormat(row, column){
        var statusid = row['deviceTypeCode'];

        return this.getDicDevTText(statusid);
     },

    devstatusFormat(row, column){
      var statusid = row['deviceStatusCode'];

      return this.getDicDevSText(statusid);
    },

    getDicDevSText(key){
      var dictemp = this.dics['4*'];
        var rolevalue={};
        rolevalue=dictemp[key];
        return rolevalue;
    },

    getDicDevTText(key){
      var dictemp = this.dics['3*'];
        var rolevalue={};
        rolevalue=dictemp[key];
        return rolevalue;
    },
    //
    // 
    // 
    searchDevice () {

      var url = '/dsdeviceM/dsdevice/pages?pageNum='+this.params.pageNum+'&pageSize='+this.params.pageSize;
      var p = JSON.parse(JSON.stringify(this.params));
      this.$http.post(url, p).then((response) => {
        var pageUser = response.data;
        this.total = pageUser.total;
        this.params.pageNum = pageUser.pageNum;
        console.log(pageUser.list)
        this.tableData = pageUser.list;
        this.shieldmgr_loading.close();
      });
    },
    dsdeviceCodeyz(e){
      this.$nextTick(() => {
        this.deviceForm.dsdeviceCode = e.replace(/[^a-zA-Z0-9_-]/g,'')
      })
      console.log(e.replace(/[^a-zA-Z0-9_-]/g,''))
      // this.deviceForm.dsdeviceCode = e.replace(/[^a-zA-Z0-9_-]/g,'')
      console.log(this.deviceForm.dsdeviceCode)
    },
    reset () {
      this.params.dsdeviceName = null;
      this.params.orgnizationName = null;
      this.searchDevice();
    },

    // 
    devicecomm (index, row) {

      this.deviceID = row.dsdeviceCode
        this.dialogFormVisiblex = true
        this.$http.get('/dsdeviceM/dsdevice/unpaired_serialNos').then((response) => {
        // 
          var isRemovedOk = response.data;
          if(isRemovedOk){
             this.newArr = isRemovedOk
          }
        });
    },
    //
    radioss(o,i){
      this.radios = o
      var num = this.$refs["o"].length
      this.$refs["o"][this.radio_index].style.background="#ffffff"
      this.$refs["o"][this.radio_index].style.color="#48576a"
      this.radio_index = i
      this.$refs["o"][i].style.background="#055476"
      this.$refs["o"][i].style.color="#ffffff"
    },
    //
    devicecommex (index, row) {
      var _this = this
        if(this.radios == ""){
          this.$message({
            message: '',
            type: 'warning'
          });
          return false
        }

        //
        this.$store.state.indexstore.dsdeviceSerialNo = this.radios;
        console.log(this.$store.state.indexstore.dsdeviceSerialNo);

        var str = {};
        str.deviceID=this.deviceID;
        str.serialNum=this.radios;
        this.$http.post('/dsdeviceM/dsdevice/register_cmd',str).then((response) => {
          // 
          var isRemovedOk = response.data;
          var tableData_i = ""
          if(isRemovedOk){
            this.$message({
              message: '',
              type: 'success'
            });
            //
            this.dialogFormVisiblex = false
            var tableData = this.tableData
            var tableData_i_new = tableData.filter(function(item,i){
              if(item.dsdeviceCode == str.deviceID){
                tableData_i = i
                return  item.dsdeviceSerialNo = _this.radios
              }
            })
            this.tableData[tableData_i] = tableData_i_new[0]

                }else{
                  this.$message.error('!'); 
                }
           });
    },


    // 
    addDevice () {
      var _this = this
      this.deviceForm.dsdeviceSerialNo='N/A'
      
      for(let key in this.dics['3*']){
         if(_this.dics['3*'][key] == ""){
            _this.deviceForm.deviceTypeCode = key
         }
      }        
      console.log(this.dics['3*'])
      this.dialogType = 'add';      
      this.WorkOrderForm = Object.assign({}, );
      this.dialogFormVisible = true;
    },
    
    cancelDevice(){
      this.ClearDeviceForm();
      this.dialogFormVisible = false;    //el-dialog
    },

    saveDevice(){

          var _this = this
          switch(this.dialogType){
             // 
            case 'add':
                {
                  var rowTmp =  [].concat(JSON.parse(JSON.stringify(this.deviceForm)));
                  var p = JSON.parse(JSON.stringify(rowTmp[0]));
                  this.$http.post('/dsdeviceM/dsdevice', p).then((response) => {
                    // 
                      var isAddedOk = response.data;
                      
                      if(isAddedOk){
                          this.$message({
                          message: "",
                          type: 'success'
                          });
                          this.dialogFormVisible = false;
                          this.searchDevice();
                          this.ClearDeviceForm();

                      }else{
                        this.$message.error('!'); 
                      }
                  });
                }
             break;
            // 
            case 'edit':
                {
                  var rowTmp =  [].concat(JSON.parse(JSON.stringify(this.deviceForm)));
                  var p = JSON.parse(JSON.stringify(rowTmp[0]));

                  this.$http.put('/dsdeviceM/dsdevice', p).then((response) => {
                    // 
                      var isUpdatedOk = response.data;
                      if(isUpdatedOk){
                          this.$message({
                          message: "",
                          type: 'success'
                          });
                          this.dialogFormVisible = false;
                          this.searchDevice();
                      }else{
                        this.$message.error('!'); 
                      }
                  });
                } 
              break;
          }
    },

    ClearDeviceForm(){
        this.deviceForm={
                deviceStatusCode: '',
                deviceTypeCode: '',
                dsdeviceCode: '',
                dsdeviceModel: '',
                dsdeviceName: '',
                // dsdeviceSerialNo: '',        
                organizationCode: '',        
              }

    },

    editShield(index, row){

        if(row['deviceStatusCode'] == "DS-OUT-01"){
            this.$message.info(""); 
            return;
        }

        this.dialogType = 'edit'; 
        var rowTmp =  [].concat(JSON.parse(JSON.stringify(row)));

        this.deviceForm = Object.assign({}, rowTmp[0]);
        this.dialogFormVisible = true;
        this.table_index = index;
    },

    delShield(index, row){
        if(row['deviceStatusCode'] == "DS-OUT-01"){
            this.$message.info(""); 
            return;
        }
        
        var p = JSON.parse(JSON.stringify(row['dsdeviceCode']))
        var parm = '/dsdeviceM/dsdevice?dsdeviceCode='+row['dsdeviceCode'];
        this.$http.delete(parm).then((response) => {
          // 
          var isRemovedOk = response.data;
          if(isRemovedOk){
                this.$message({
                 message: "",
                 type: 'success'
                 });
                this.tableData.splice(index, 1);
                }else{
                  this.$message.error('!'); 
                }
           });
    },

    sizeChange(val) {
      this.params.pageSize = val;
      this.searchDevice();
    },
    currentChange(val) {
      this.params.pageNum = val;
      this.searchDevice();
    },
  },
}

</script>
<style>
  .el-pagination {
    background-color: rgb(249, 249, 249);
    text-align: center;
    margin-top: 30px;
  }
  .el-message-box__btns .cancel {
    float: right;
    margin-left: 10px;
  }
  .active{
    background: #055476;
  }
  .el-dialog__header{
    background: #006086 !important;
    padding: 10px 10px 10px 20px !important;
  }
  .el-dialog__title{
    color: #ffffff !important;
  }
</style>



